.create,
.design {
  margin: 0;
}
.create {
  color: var(--color-skyblue);
}
.inspire {
  margin: 0;
  color: var(--color-mediumturquoise);
}
.design-create-inspire-container {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-53xl);
}
.welcome {
  align-self: stretch;
  position: relative;
  color: var(--color-white);
}
.my-projects {
  position: relative;
  line-height: 18px;
}
.button {
  border-radius: var(--br-31xl);
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-5xl) var(--padding-29xl);
  cursor: pointer;
  color: var(--color-gray);
}
.text {
  position: absolute;
  top: 117px;
  left: 64px;
  width: 359px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.icon-twitter {
  position: absolute;
  height: 3.67%;
  width: 3.09%;
  top: 89.11%;
  right: 93.22%;
  bottom: 7.22%;
  left: 3.69%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  cursor: pointer;
}
.frame-child {
  position: relative;
  border-radius: var(--br-21xl);
  width: 172px;
  height: 192px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-parent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.image-gallery {
  position: absolute;
  top: 117px;
  left: 607px;
  width: 742px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-lg);
}
.div {
  position: relative;
  background-color: var(--color-darkslategray);
  width: 100%;
  height: 1185px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-pink);
  font-family: var(--font-poppins);
}
